body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  position: absolute;
  z-index: -5;
}
input,
textarea,
button {
  font-family: "roboto";
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
li {
  list-style: none;
}
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 34px;
  font-weight: 400;
}
:root {
  /* Основные цвета текста */
  --color-text-primary: #747474;
  --color-text-secondary: #d1d1d1;

  /* Цвета фона */
  --color-bg-primary: #f9f4f4;
  --color-bg-hover: #e9feea;

  /* Цвета для title и фильтров */
  --color-title-primary: rgb(213, 184, 182);

  /* Цвет ошибки */
  --color-error: red;

  --color-bg-main: rgb(223, 220, 215);
}

@media (max-width: 480px) {
  body {
    font-size: 8px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  body {
    font-size: 10px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  body {
    font-size: 12px;
  }
}
